import Vue from 'vue'
import App from './App.vue'

// VUEROUTER CONFIGURATION
import VueRouter from 'vue-router';
import Router from "./router.js";

Vue.use(VueRouter);

// ALERTIFY
import VueAlertify from 'vue-alertify';

Vue.use(VueAlertify);


Vue.config.productionTip = false

new Vue({
  render: h => h(App),
  router: Router,
}).$mount('#app')
