<template lang="html">
  <div class="contact-app">
    <div class="container">
      <h1 class="text-center">Contact</h1>

      <div class="contact-grid mt-5">
        <div>
          <input class="contact-input" type="text" name="name" placeholder="Name" v-model="name">
          <input class="contact-input" type="email" name="email" placeholder="Email" v-model="email">
          <textarea class="contact-input" name="message" rows="8" placeholder="Message" v-model="message"></textarea>
          <input type="submit" class="ghost-cta-button" value="Contact Us!" v-on:click="sendEmail">
        </div>
        <p>
          We guarantee a response within 2 business days!
        </p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      name: "",
      email: "",
      message: "",
    }
  },
  methods: {
    sendEmail: function() {
      fetch(`/api/send-email/?name=${this.name}&email=${this.email}&message=${this.message}`, {
        method: "POST",
      });

      this.name = "";
      this.email = "";
      this.message = "";

      this.$alertify.success("Email successfully sent!");
    }
  }
}
</script>

<style lang="css" scoped>
  .contact-app {
    padding: 80px 0;
  }

  .contact-app * {
    color: white;
  }

  .contact-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 50px;
  }

  .contact-input {
    display: block;
    background-color: transparent;
